let vm = null;
export default {
  name: "homeSwiper",
  props: {
    imgArr: {
      type: Array
    }
  },
  data() {
    return {
      swiperOption: {
        //æ¾ç¤ºåé¡µ
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        //å¼å¯æ éå¾ªç¯
        loop: true,
        // lazy: true,
        autoplay: {
          delay: 5000
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        on: {
          click(event) {
            let item = event.target.id;
            let { relation_id, type, url } = JSON.parse(item);
            console.log(relation_id, "relation_id", type);
            if (type == "no_jump") {
              return;
            }
            if (type == "product") {
              console.log("******");
              vm.$router.push({
                path: "/product/" + relation_id
              });
              return;
            }
            if (type == "activity") {
              console.log("activity");
              vm.$router.push({
                path: "/activitys",
                query: {
                  id: relation_id
                }
              });
              return;
            }
            if (type == "invitation_page") {
              vm.$router.push({ path: "/invitation" });
              return;
            }
            if (type == "webview") {
              window.location = url;
              return;
            }
            if (type == "category") {
              vm.$router.push({
                path: "/categories",
                query: {
                  category: relation_id
                }
              });
              return;
            }
            if (type == "tag") {
              vm.$router.push({
                path: "/categories",
                query: {
                  category: relation_id
                }
              });
              return;
            }
          }
        }
      },
      bannerList: [],
      bannerLoading: false
    };
  },
  created() {
    vm = this;
  },
  mounted() {
    // this.getBanner();
  },
  methods: {
    //è·åbanner
    getBanner() {
      this.bannerLoading = true;
      this.$apis
        .getLayout()
        .then(res => {
          if (res.status == 1) {
            this.bannerLoading = false;
            let list = res.data.items;
            for (var index = 0; index < list.length; index++) {
              if (list[index].type == "trending") {
                if (list[0].layout[0].type == "banners") {
                  this.bannerList = list[0].layout[0].list;
                }
              }
            }
          } else {
            this.bannerLoading = false;
            this.common.msgBox(this, res.msg, 3);
          }
        })
        .catch(error => {
          this.bannerLoading = false;
          console.log(error);
        });
    },
    toDetail(obj) {
      console.log('åä¸ª',obj)
      let { relation_id, type, url } = obj;
      if (type == "no_jump") {
        return;
      }
      if (type == "product") {
        console.log("******");
        this.$router.push({
          path: "/product/" + relation_id
        });
        return;
      }
      if (type == "activity") {
        console.log("activity");
        this.$router.push({
          path: "/activitys",
          query: {
            id: relation_id
          }
        });
        return;
      }
      if (type == "invitation_page") {
        this.$router.push({ path: "/invitation" });
        return;
      }
      if (type == "webview") {
        window.location = url;
        return;
      }
      if (type == "category") {
        this.$router.push({
          path: "/categories",
          query: {
            category: relation_id
          }
        });
        return;
      }
      if (type == "tag") {
        this.$router.push({
          path: "/categories",
          query: {
            category: relation_id
          }
        });
        return;
      }
    }
  }
};
