export default {
  name: "exclusiveBanner",
  props: {
    
  },
  data() {
    return {
      swiperOption: {
        //è®¾ç½®ç¹å»ç®­å¤´
        paginationClickable: true,
        navigation: {
          prevEl: ".swiper-button-prev",
          nextEl: ".swiper-button-next"
        },
        //å¼å¯æ éå¾ªç¯
        loop: true
      },
      imgArr: []
    };
  },
  created() {},
  mounted() {},
  methods: {
	toDetail(obj){
        if(obj.id){
          this.$router.push({
            path:'/product/'+obj.id,
          })
        }
        if(obj.active){
          this.$router.push({
            path:'/activityTemplate/_page',
            query:{
              id: obj.active
            }
          })
        }
        if(obj.invite){
          this.$router.push({path:'/invitation'})
        }
      }
    }
  }
