export default {
  name: "useBar",
  props: {},
  data() {
    return {
      showpPrivacy: false
    };
  },
  mounted() {
    // showpPrivacy
    let showprivacy = localStorage.getItem("showpPrivacy");
    if (showprivacy == "1") {
      this.showpPrivacy = false;
    } else {
      this.showpPrivacy = true;
    }
  },
  methods: {
    closepbar() {
      localStorage.setItem("showpPrivacy", "1");
      this.showpPrivacy = false;
    }
  }
};
