export default {
  name: "womenClothing",
  props: {
    womenClothingObj: {
      type: Array,
      value: []
    }
  },
  data() {
    return {
      womenClothingObj: {}
    };
  },
  mounted() {
    // this.getRecommend();
  },
  methods: {
    getRecommend() {
      let thif = this;
      this.$apis
        .getDealsWeek({ tag: 3779, page: 1, per_page: 100 })
        .then(res => {
          console.log(res, "getDealsWeek");
          if (res.status == 1) {
            thif.womenClothingObj = res.data;
          } else {
            thif.common.msgBox(thif, res.msg, 3);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    toShopDetail(id) {
      this.buryingPoint.productClick(id);
      this.$router.push({
        path: "/product/" + id
      });
    }
  }
};
